<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
            :src="require('../../assets/logo-white.png')"
            contain
            height="80"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1
            style="font-size: 1.5rem"
            class="font-weight-bold profile-title text-center"
          >
            Estado de tus tramites
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <div class="search">
            <v-text-field
              label="Ingresa el código del documento solicitado."
              outlined
              dense
              append-icon="mdi-magnify"
              hide-details
              @input="onSearch"
            ></v-text-field>
          </div>
        </v-col>
      </v-row>

      <v-row class="text-center">
        <v-col>
          <table class="table table-main">
            <thead>
              <tr>
                <th>
                  <div>Documentos</div>
                </th>
                <th>Estado</th>
                <th>Pendiente</th>
                <th>Historial</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in operations" :key="item.id">
                <td class="text-left">
                  <div style="color: #084068">{{ item.document_name }}</div>
                  <div>COD: {{ item.id | padleft }}</div>
                </td>
                <td>{{ item.status.name }}</td>
                <!-- 6	Pagado -->
                <td v-if="item.status_id === 6" @click="goToPayment(item)">
                  <img
                    :src="require('../../assets/icons/completa_datos.jpeg')"
                    alt="img"
                  />
                  Completar datos
                </td>
                <!-- 3	Pendiente de pago -->
                <td v-if="item.status_id === 3" @click="goToPayment(item)">
                  <img
                    :src="require('../../assets/icons/table-01.svg')"
                    alt="img"
                  />
                  Completar pago
                </td>

                <td v-if="item.status_id !== 3 && item.status_id !== 6"></td>
                <td @click="goToDetails(item)">
                  <img
                    :src="require('../../assets/icons/table-06.svg')"
                    alt="img"
                  />
                  Resumen
                </td>
              </tr>
              <tr v-if="operations.length === 0">
                <td colspan="10">No se encontraron registros.</td>
              </tr>
            </tbody>
          </table>

          <div class="mt-3">
            <v-btn @click="goToOperations" color="primary"
              >Busca un tramite</v-btn
            >
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import axios from "axios";

export default {
  name: "OperationIndex",
  data: () => ({
    operations: [],
  }),
  computed: {
    ...mapGetters(["user"]),
  },
  created() {
    this.loadOperations();
  },
  methods: {
    loadOperations(search = null) {
      let url = "operations";

      if (search !== null) {
        url += "?search=" + search;
      }

      axios.get(url).then((response) => {
        this.operations = response.data.data;
      });
    },
    onSearch(input) {
      this.loadOperations(input);
    },
    goToPayment(item) {
      //============== COLOCAMOS EL FORMULARIO PARA EL PAGO
      if (item.status_id === 6 && !item.document.variable_price) {
        this.goToFormData(item);
        return;
      }

      this.$router.push({ name: "OperationPayment", params: { id: item.id } });
    },
    goToFormData(item) {
      this.$router.push({ name: "DocumentsData", params: { id: item.id } });
    },
    goToDetails(item) {
      //============== COMENTAMOS LA VISTA DEL FORMULARIO
      // if (item.status_id === 6 && !item.document.variable_price) {
      //   this.goToFormData(item);
      //   return;
      // }

      this.$router.push({ name: "OperationInvoice", params: { id: item.id } });
    },
    goToOperations() {
      this.$router.push({ name: "DocumentsIndex" });
    },
    canSeePayment(item) {
      let currentStatus = parseInt(item.status_id);

      return currentStatus === 3;
    },
  },
};
</script>
<style lang="scss" scoped>
.table-main {
  thead {
    tr {
      th {
        font-size: 0.8rem;
        position: relative;
      }

      th:nth-child(1) {
        border-top-left-radius: 1rem;
        border-bottom-left-radius: 1rem;

        img {
          position: absolute;
          left: calc(100% / 2 - 100px);
          top: -5px;
        }
      }

      th:nth-child(4) {
        border-top-right-radius: 1rem;
        border-bottom-right-radius: 1rem;

        img {
          position: absolute;
          left: 0;
          top: -5px;
        }
      }

      th:nth-child(1) div {
        color: #707578;
        border-radius: 1rem;
        background-color: white;
      }
    }
  }
}
</style>